import * as React from 'react';
import CButton from '@chakra-ui/core/dist/Button';

export function PrimaryButton({ color, children, ...props }) {
  return (
    <CButton
      backgroundColor={`${color}.600`}
      color={`${color}.50`}
      // boxShadow="0 4px 6px -1px rgba(42, 105, 172, 0.6), 0 2px 4px -1px rgba(42, 105, 172,0.06)"
      shadow="md"
      fontWeight="bold"
      _hover={{ backgroundColor: `${color}.700` }}
      _active={{ backgroundColor: `${color}.800` }}
      {...props}
    >
      {children}
    </CButton>
  );
}

export function SecondaryButton({ color, children, ...props }) {
  return (
    <CButton
      backgroundColor="#fff"
      color={`${color}.500`}
      borderColor={`${color}.500`}
      borderWidth={2}
      borderStyle="solid"
      // boxShadow="0 4px 6px -1px rgba(42, 105, 172, 0.6), 0 2px 4px -1px rgba(42, 105, 172,0.06)"
      shadow="md"
      fontWeight="bold"
      _hover={{ color: `${color}.700`, borderColor: `${color}.700` }}
      _active={{ color: `${color}.800`, borderColor: `${color}.800` }}
      {...props}
    >
      {children}
    </CButton>
  );
}
